import { SubtleButton } from '@/components/buttons/SubtleButton';
import UnderlineHeading from '@/components/heading/UnderlineHeading';
import ImportIcon from '@/components/icons/file/ImportIcon';
import AddShiftForm from '../forms/addShift/AddShiftForm';
import { useParams } from 'react-router-dom';
import { LoadingSpinner } from '@/components/Loading/LoadingSpinner';
import { useEffect, useState } from 'react';
import { apiGet } from '@/lib/axios/axios-utils';
import { endpoints } from '@/lib/axios/axios-global';
import { shiftInfo } from '../types/shift.types';

export default function EditShiftPage() {
    const params = useParams();
    const { id } = params;
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState<string | null>(null);
    const [shift, setShift] = useState<shiftInfo | null>(null);

    useEffect(() => {
        const fetchShift = async () => {
            try {
                const response = await apiGet({
                    endpoint: endpoints.shifts.show + id,
                });
                setShift(response.data.data);
            } catch (error) {
                setError('Failed to load shift details');
            } finally {
                setLoading(false);
            }
        };
        if (id) {
            fetchShift();
        } else {
            setError('Invalid shift ID');
            setLoading(false);
        }
    }, [id]);

    if (loading) {
        return <LoadingSpinner />;
    }

    if (error) {
        return (
            <div className="mt-6 mb-[102px] text-center py-20">
                <h2 className="text-xl text-red-500">{error}</h2>
            </div>
        );
    }

    if (!shift) {
        return (
            <div className="mt-6 mb-[102px] text-center py-20">
                <h2 className="text-xl">Shift not found</h2>
            </div>
        );
    }

    return (
        <div className="mt-6 w-full">
            <div className="flex flex-row justify-between mb-6">
                <UnderlineHeading text="اضافة وردية" />
                <SubtleButton className="bg-primery-blue-50 text-base font-medium leading-[125%] text-primery-blue-500 border border-primery-blue-50">
                    <ImportIcon />
                    استيراد ملف بيانات
                </SubtleButton>
            </div>
            <AddShiftForm shift={shift} />
        </div>
    );
}
